<template>
  <div class="app-container subscribe">
    <el-card>

      <el-form :inline="true" :model="listQuery" class="demo-form-inline">
        <el-form-item label="姓名">
          <el-input v-model="listQuery.name" placeholder="请输入姓名"></el-input>
        </el-form-item>
        <!-- <el-form-item label="联系方式">
          <el-input v-model="listQuery.mobile" placeholder="请输入联系"></el-input>
        </el-form-item> -->
        <el-form-item label="门店">
          <el-select v-model="listQuery.store_id" placeholder="选择门店">
            <el-option label="龙珠店" value="2"></el-option>
            <el-option label="海德店" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="search">查询</el-button>
        </el-form-item>
      </el-form>

      <el-table v-loading.body="listLoading" element-loading-text="拼命加载中" stripe highlight-current-row  :data="list" style="width: 100%">
        <el-table-column label="ID" prop="id" width="100">
        </el-table-column>
        <el-table-column label="门店">
          <template scope="scope">
            <span>{{ scope.row.store_id === 1 ? '英威尔海德店' : '英威尔龙珠店' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="姓名" prop="name">
        </el-table-column>
        <el-table-column label="联系方式" prop="mobile">
        </el-table-column>
        <el-table-column label="预约到店日期">
          <template scope="scope">
            <span>{{ formatDateTime(scope.row.book_time) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="新增时间" prop="add_time">
          <template scope="scope">
            <span>{{ formatDateTime(scope.row.add_time) }}</span>
          </template>
        </el-table-column>
      </el-table>

      <div v-show="!listLoading" class="pagination-container">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="page" :page-sizes="[5, 10]" :page-size="size" layout="total, sizes, prev, pager, next" :total="total">
        </el-pagination>
      </div>

    </el-card>
  </div>
</template>

<style>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}

.subscribe .pagination-container {
  margin-top: 10px;
}
</style>

<script>
import { dataLists } from '@/api/recvData';
import { formatDateTime } from '@/utils';

export default {
  data() {
    return {
      // 总条数
      total: null,
      // 页码
      page: 1,
      size: 10,
      // 表格数据参数
      listQuery: {
        offset: '0,10',
        // limit: 20,
        store_id: undefined,
        // mobile: undefined,
        name: undefined
        // sort: '+id'
      },
      list: null,
      listLoading: true
    }
  },
  created() {
    this.getList();
  },
  methods: {
    getList() {
      this.listLoading = true;
      dataLists(this.listQuery).then(response => {
        this.list = response.data.list;
        this.total = response.data.total;
        console.log(this.listQuery, this.list);
        this.listLoading = false;
      })
    },
    handleSizeChange(val) {
      this.size = val;
      this.listQuery.offset = (this.page - 1) * this.size + ',' + this.size;
      this.getList()
    },
    handleCurrentChange(val) {
      this.page = val;
      this.listQuery.offset = (this.page - 1) * this.size + ',' + this.size;
      this.getList()
    },
    search() {
      this.getList();
    },
    formatDateTime(time) {
      return formatDateTime(time)
    }
  }
}
</script>



